import React, { useState } from 'react';



const Contect = () => {

const [data, setData] = useState({
    fullname:'',
    phone:'',
    email:'',
    msg:'',
});

const inputEvent=(event)=>{
const{name, value} = event.target;

setData((prevalue)=>{
    return{...prevalue,
        [name]:value,
    
    }
})
}

const formSubmit=(e)=>{
e.preventDefault();
alert(`my name is ${data.fullname} My mobile number is ${data.phone} And My Email is ${data.email}`);
}

    return (
        <>
            <div className='my-5'>
                <h1 className='text-center'>Conect Us</h1>
            </div>
            <div className='container contect__div'>
                <div className='row'>
                    <div className='col-md-6 col-10 mx-auto'>
                        <form onSubmit={formSubmit}>
                            <div class="form-group">
                                <label for="exampleFormControlInput1">Full Name</label>
                                <input type="text" className="form-control" id="exampleFormControlInput1"
                                    name="fullname"
                                    value={data.fullname}
                                    onChange={inputEvent}
                                    placeholder="Ener your name" />
                            </div>
                            <div className="form-group">
                                <label for="exampleFormControlInput1">Phone</label>
                                <input type="number" className="form-control" id="exampleFormControlInput1"
                                    name="phone"
                                    value={data.phone}
                                    onChange={inputEvent}
                                    placeholder="Mobile No" />
                            </div>
                            <div className="form-group">
                                <label for="exampleFormControlInput1">Email address</label>
                                <input type="email" className="form-control" id="exampleFormControlInput1"
                                    name="email"
                                    value={data.email }
                                    onChange={inputEvent}
                                    placeholder="name@example.com" />
                            </div>
                            <div className="form-group">
                                <label for="exampleFormControlTextarea1">Massage</label>
                                <textarea className="form-control" id="exampleFormControlTextarea1"
                                    name="msg"
                                    value={ data.msg}
                                    onChange={inputEvent}
                                    rows="3"></textarea>
                                <div className='my-3 col-12'>
                                    <button type="submit" className="btn btn-primary">Submit</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </>
    );
};
export default Contect;